<template>
  <div></div>
</template>

<script>
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  auth: false,
  created() {
    this.logout()
  },
  methods: {
    async logout() {
      await this.$auth.logout()
      this.$router.push('/')
    }
  }
}
</script>

